import React from 'react';
import "./Mars.css";
import Frame from '../../shared/Frame/Frame';

// Babylon
import { AnaglyphUniversalCamera, HemisphericLight, Vector3, Color4 } from 'babylonjs';
// import BabylonScene from '../../shared/Babylon.jsx';
// import { WaterMaterial } from 'babylonjs-materials';

// Other components
// import Frame from '../../Universal/Frame/Frame';
// import FrameSimple from '../../Universal/Frame/FrameSimple';

// Material UI
// import { makeStyles } from '@material-ui/core/styles';
// import Grid from '@material-ui/core/Grid';
// import TextField from '@material-ui/core/TextField';
// import Button from '@material-ui/core/Button';

import Glasses from '../../shared/Glasses/Glasses';

var camera;

class Mars extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight
    }
  }


  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }

  updateDimensions() {
    this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight });
  }

  onSceneReady(scene) {
    // const camera = new UniversalCamera("UniversalCamera", new Vector3(0, 1, -25), scene);
    camera = new AnaglyphUniversalCamera("af_cam", new Vector3(0, 8, -5), 0.033, scene);
    scene.clearColor = new Color4(0, 0, 0, 0);
    // scene.clearColor = new Color3(1, 0, 1);


    camera.setTarget(new Vector3(0, 8, 0));

    const canvas = scene.getEngine().getRenderingCanvas();
    camera.attachControl(canvas, true);

    const light = new HemisphericLight("light1", new Vector3(0, 1, 0), scene);
    light.intensity = 0.7;



  }





  onRender(scene) {
    let divFps = document.getElementById("fps");
    divFps.innerHTML = scene.getEngine().getFps().toFixed() + " fps";
  }

  render() {

    // <div className="smallCrater"></div>
    return (
      <div className="Mars">
        <div className="Frame-box">
          <Frame title="" content={
              <div className="bigCrater"></div>
            }
            width={400} height={700} x={100} y={100}
            />

          <Frame title="" content={
              <div className="blueChair"></div>
            }
            width={200} height={200} x={400} y={300}
            />
            <Frame title="" content={
                <div className="blueChair flippedY"></div>
              }
              width={200} height={200} x={500} y={400}
              />
          <Glasses />
        </div>
      </div>
    )
  }
}


export default Mars;
