import React from 'react';
import Frame from '../../shared/Frame/Frame';
import './MacbookAir.css';

import Grid from '@material-ui/core/Grid';
import Slider from '@material-ui/core/Slider';
// import indigo from '@material-ui/core/colors/indigo';

import ReactPlayer from 'react-player'
import mainVid from  "./assets/noframe.mp4";
import cloudsVid from  "./assets/clouds3d.mp4";

import Glasses from '../../shared/Glasses/Glasses';

class MacbookAir extends React.Component {
  // https://codepen.io/JohJakob/pen/YPxgwo
  constructor(props) {
    super(props);

    this.state = {
      videoSpeed : 1.0,


    }

    this.sliderOG= {x: 0, y: 0};
    this.mainOG= {x: 0, y: 0};

    this.sliderW = 80;

    this.setSpeed = this.setSpeed.bind(this);
    this.getMainWindowDim = this.getMainWindowDim.bind(this);
    this.getSliderDim = this.getSliderDim.bind(this);
    this.getMainWindow = this.getMainWindow.bind(this);
    this.getSliderWindow = this.getSliderWindow.bind(this);
  }

  componentDidMount() {
    console.log("thanks to Google for the Umbrella palm tree", "https://poly.google.com/view/8Qwgncb6dJT", "licensed under CC BY 2.0")
  }
  // componentDidMount() {
  //   this.updateDimensions();
  //   window.addEventListener("resize", this.updateDimensions.bind(this));
  // }
  //
  // componentWillUnmount() {
  //   window.removeEventListener("resize", this.updateDimensions.bind(this));
  // }
  //
  // updateDimensions() {
  //   this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight });
  // }

  setSpeed(speed) {
    this.setState({videoSpeed: speed});
  }

  getMainWindowDim() {
    // console.log("dim", this.props.dimensions)
    const {windowWidth, windowHeight, device, orientation} = this.props.dimensions;
    const originalDim = {w: 840, h: 540};
    const aspectRatio = originalDim.w/originalDim.h;

    //(account for header, bottom slider, buffer in between...)
    // this is the space for the main window
    let minSpacing = windowWidth*.05;
    let availableHeight = windowHeight-30-85-minSpacing*3;
    let availableWidth = windowWidth - minSpacing*2;

    let windowAspectRatio = availableWidth/availableHeight;

    let windowDim = {w: originalDim.w, h: originalDim.h, x:0, y: 0};

    if (device === "desktop" || device === "tablet") {
      if (availableWidth < originalDim.w) windowDim.w = availableWidth;
      windowDim.h = originalDim.h * windowDim.w/originalDim.w;
      windowDim.x = windowWidth/2 - windowDim.w/2;
      windowDim.y = (windowHeight - windowDim.h)/4;
    }
    else if (device === "mobile") {

      if (orientation === "landscape") {
        availableHeight = windowHeight-30-21-minSpacing*2;
        availableWidth = windowWidth - minSpacing*3 - 64; // width of slider
      }

      if (windowAspectRatio > aspectRatio) {
        // max device height out
        windowDim.h = availableHeight;
        windowDim.w = originalDim.w * windowDim.h/originalDim.h;
      } else {
        // max device height width
        windowDim.w = availableWidth;
        windowDim.h = originalDim.h * windowDim.w/originalDim.w;
      }

      windowDim.x = windowWidth/2 - windowDim.w/2;
      windowDim.y = (windowHeight - windowDim.h)/4;
      if (orientation === "landscape") {
        windowDim.x = (windowWidth - windowDim.w-this.sliderW)/3;
        windowDim.y = (windowHeight - windowDim.h - 21 - 30)/2;
      }

    }


    return windowDim;
  }

  getSliderDim(windowDim) {
    const {device, orientation, windowWidth, windowHeight} = this.props.dimensions;
    let dim = {w: 250, h: 64, x: 0, y: 0, vertical:false};
    if (device === "desktop" || device === "tablet" || (device ==="mobile" && orientation === "portrait")) {
      dim.x = (windowWidth - dim.w)/2;
      let remainingSpace = windowHeight - windowDim.h - windowDim.y;
      let buffer = (remainingSpace - dim.h - 21)/2;
      dim.y = windowDim.h + windowDim.y + buffer;
    }
    else {
      dim.w = this.sliderW;
      dim.h = windowDim.h;
      dim.vertical = true;
      let remainingSpace = windowWidth - windowDim.w - windowDim.x;
      let buffer = (remainingSpace - dim.w)/2;
      dim.x = windowDim.w + windowDim.x + buffer;
      dim.y = (windowHeight-30- dim.h-21)/2;
    }
    return dim;
  }

  getMainWindow() {
    let windowDim = this.getMainWindowDim();

    let dx = this.props.dimensions.flipped?-this.mainOG.x + windowDim.x: 0;
    let dy =  this.props.dimensions.flipped?-this.mainOG.y + windowDim.y: 0;

    if (this.props.dimensions.windowWidth <= 0 || windowDim.w <= 0) {
      return (<div></div>);
    }
    else {
      if (this.mainOG.x === 0) {
        this.mainOG.x = windowDim.x;
        this.mainOG.y = windowDim.y;
      }
      return (
        <Frame title="macbook air" content={
            /*<video width={dimW-2} height={dimH} muted loop autoPlay><source src={videoDimURL} type="video/mp4"></source></video>*/
            <ReactPlayer
              className={"react-player mainContent"}
              playing
              muted
              loop
              width={windowDim.w}
              height={windowDim.h}
              url={mainVid}
              playbackRate={this.state.videoSpeed}
              />
          }
          width={windowDim.w+2} height={windowDim.h} x={windowDim.x} y={windowDim.y} px={windowDim.x} py={windowDim.y}
          />
      )
    }
  }

  getSliderWindow() {
    let windowDim = this.getMainWindowDim();
    let sliderDim = this.getSliderDim(windowDim);
    // let offsetMain = {
    let dx = this.props.dimensions.flipped?100: 0;
    let dy =  this.props.dimensions.flipped?400: 0;


    // }
    if (this.props.dimensions.windowWidth <= 0) {
      return(<div></div>);
    }
    else {
      return(
        <Frame title="" icon="&#58160;" content={<div className="sliderBox"><ContinuousSlider callback={this.setSpeed} orientation={sliderDim.w<200?"vertical":"horizontal"} /></div>}
          width={sliderDim.w} height={sliderDim.h} x={sliderDim.x} y={sliderDim.y} px={sliderDim.x} py={sliderDim.y}
          />
      );
    }

  }

  render() {



    return (
      <div className="MacbookAir">
        <div className="Frame-box">
          {/*<ReactPlayer
            className={"react-player backgroundCover"}
            playing
            muted
            loop
            width="100%"
            height="100%"
            url={videoBackgroundURL}
            playbackRate={this.state.videoSpeed}
            />*/}
            <video autoPlay muted loop className="backgroundCover">
              <source src={cloudsVid} type="video/mp4" ></source>
              Your browser does not support HTML5 video.
            </video>
            {this.getMainWindow()}
            {this.getSliderWindow()}
          </div>
          <Glasses />
        </div>
      );
    }

  }


  function ContinuousSlider(props) {
    // const classes = useStyles();
    const [value, setValue] = React.useState(1.0);

    const handleChange = (event, newValue) => {
      setValue(newValue);
      props.callback(newValue);
    };

    let style = {};
    style.height = props.orientation==="vertical"?"80px":"auto";
    style.width = props.orientation==="vertical"?"auto":"100px";

    return (
      <div className="Slider">
        <Grid container spacing={2}>
          <Grid item>
            <span className="emoji-slider" role="img" aria-label="cloud">☁️</span>
          </Grid>
          <Grid item xs>
            <Slider value={value} onChange={handleChange} orientation={props.orientation} style={style} color='primary' aria-labelledby="continuous-slider" step={0.1} min={0.0} max={2.0} defaultValue={1.0} />
          </Grid>
          <Grid item>
            <span className="emoji-slider" role="img" aria-label="dashing away">💨</span>
          </Grid>
        </Grid>
      </div>
    );
  }

  export default MacbookAir;
