///////////////////// p5.js
import P5Wrapper from 'react-p5-wrapper';
import sketch from './wetSketch';
// import Stair from './Stair';

// palm https://poly.google.com/view/ficLBIjGliK
import Glasses from '../../shared/Glasses/Glasses';

class WetStreams extends React.Component {

  constructor(props) {
    super(props);

    this.startW = window.innerWidth;
    this.startH = window.innerHeight;

    this.dim = 350;
    this.state = {
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
      showerFrame: {
        w: this.dim,
        h: this.dim,
        x: (window.innerWidth-this.dim)*.2,
        y: (window.innerHeight-30-this.dim)*.3
      },
      deltaPositions: [
        {x: 0, y: 0},
        {x: 0, y: 0},
        {x: 0, y: 0},
        {x: 0, y: 0},
        {x: 0, y: 0},
        {x: 0, y: 0}
      ],
      isPlaying0: true,
      isPlaying1: true,
      percent0: 0,
      percent1: 1
    }
    // this.onRender = this.onRender.bind(this);
    this.handleDrag = this.handleDrag.bind(this);

  }

  handleDrag = (index, ui) => {
    let dx = ui.deltaX;
    let dy = ui.deltaY;
    // 1. Make a shallow copy of the items
    let dps = [...this.state.deltaPositions];
    // 2. Make a shallow copy of the item you want to mutate
    let dp = {...dps[index]};
    // 3. Replace the property you're intested in
    dp.x += dx;
    dp.y += dy;
    // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
    dps[index] = dp;
    // 5. Set the state to our new copy
    this.setState({deltaPositions:dps});
  };


  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }

  updateDimensions() {
    let {showerFrame} = this.state;
    showerFrame.x = (window.innerWidth-this.dim)*.2;
    showerFrame.y = (window.innerHeight-30-this.dim)*.3;
    this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight });
    this.setState({showerFrame: showerFrame});
  }



  render() {
    let {windowWidth, windowHeight, showerFrame} = this.state;
    let origins = [];
    let space = 40;
    let startX = (windowWidth-3*showerFrame.w-2*space)/2;
    // if (startX < 0) startX = 0;

    let spaceV = 40;
    let startY = (windowHeight-60-2*showerFrame.h-spaceV)/2;
    // if (startY < 0) startY = 0;

    for (let i = 0; i < 3; i++) {
      origins[i] = {x: startX + i*(showerFrame.w + space), y: startY};
      origins[i+3] = {x: startX + i*(showerFrame.w + space), y: startY + showerFrame.h + spaceV };
    }

    let imgs = [7, 1, 3, 4, 8, 10];
    let offsets = [{x:215,y:320}, {x:215,y:215}, {x:50,y:195}, {x:140,y:250}, {x:260,y:335}, {x:240,y:315}];
    let velocities = [
      {velX: {min: -.8, max:5}, velY: {min: -.5, max:0}},
      {velX: {min: -5, max:2}, velY: {min: -.5, max:0}},
      {velX: {min: -5, max:.8}, velY: {min: -.5, max:0}},
      {velX: {min: -5, max:.8}, velY: {min: -.5, max:0}},
      {velX: {min: -5, max:5}, velY: {min: -.5, max:0}},
      {velX: {min: -.5, max:5}, velY: {min: -.5, max:0}}
    ];
    let degrees = [0, 60, 90, 80, 40, 0];

    return (
      <div className="WetStreams">
        <div className="Frame-box">


          {/* https://media.giphy.com/media/Kk4f3yrTot95K/giphy.gif*/}


          {imgs.map((img, i) => {
            let title = "";
            if (i === 0) title = "wet streams";
            let dx = (this.state.windowWidth - this.startW)/2;
            let dy = (this.state.windowHeight - this.startH)/2;
            return getShower(i, origins[i].x, origins[i].y, dx, dy, showerFrame.w, showerFrame.w, img, this.handleDrag, title);
          })};


          <P5Wrapper className="p5sketch" sketch={sketch}
            origins={origins}
            offsets={offsets}
            velocities={velocities}
            deltas={this.state.deltaPositions}
            degrees={degrees}
            />


        </div>
        <Glasses />
      </div>
    )
  }
}

function getShower(id, x, y,dx, dy, w, h, img, dragHandler, title, ) {
  //"./assets/images/tub/" + img + ".png"

  return(
    <Frame key={id} id={id} onDrag={dragHandler} title={title} content={
        <div className={id===5?"showerhead flippedX":"showerhead"}>
          <img src={require("./assets/showerHeads/" + img + ".png")} alt="shower head" />
        </div>
      }
      width={w} height={h} x={x} y={y} dx={dx} dy={dy}
      />
  )

}




export default WetStreams;
